import "./intro.scss";
import {init} from "ityped"
import { useEffect, useRef } from "react";


export default function Intro() {

    const textRef = useRef()
    useEffect(() => {
        init(textRef.current,{
            showCursor: true,
            backDelay: 1000,
            backSpeed: 60,
            strings: ["Dev Zai","Developer","Gamer","Designer"]
        })
    },[])

    return (
        <div className="intro" id="intro">
            <div className="left">
                <div className="imgContainer">
                    <img src="assets/tan2.png" alt=""/>
                </div>
            </div>
            <div className="right">
                <div className="wrapper">
                    <h2>Welcome ! I'm </h2>
                    <h1>Tan <span ref={textRef}></span> </h1>
                    <h3>Let's explore about me !</h3>
                </div>
                <a href="#portfolio">
                    <img src="assets/down.png" alt=""/>
                </a>

            </div>
        </div>
    )
}
